import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TokenStorageService } from './token-storage.service';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class RetailService {
  basePath: string = environment.microServiceURL;

  constructor(
    private httpClient: HttpClient,
    private tokenService: TokenStorageService
  ) {}

  getCurrencies() {
    return this.httpClient.get<any>(`${this.basePath}/icCurrency`);
  }

  getBranch() {
    return this.httpClient.get<any>(`${this.basePath}/branch`);
  }

  createAppointment(payload: any) {
    return this.httpClient.post<any>(
      `${this.basePath}/transaction/scheduleAppointment`,
      payload
    );
  }

  getTransactionDetailsById(transId: number) {
    return this.httpClient.get<any>(
      `${this.basePath}/fetchByTransId/${transId}`
    );
  }

  generateQRCode(payload): Observable<Blob> {
    return this.httpClient.post(
      `${this.basePath}/token/qr-code-generator`,
      payload,
      {
        responseType: 'blob',
      }
    );
  }

  addToWallet(payload: any) {
    return this.httpClient.put<any>(
      `${this.basePath}/token/updateAddToWallet`,
      payload
    );
  }
}
