import { ProfileData } from "../data/profile-data";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { of as observableOf, Observable } from "rxjs";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class ProfileService extends ProfileData {
  screen_privilege: any;

  constructor(private http: HttpClient) {
    super();
  }

  getProfileInfo(): Observable<any[]> {
    if (this.screen_privilege) {
      return observableOf(this.screen_privilege);
    } else {
      return this.getProfile();
    }
  }

  private getProfile(): Observable<any> {
    return this.http
      .get("assets/json/profile.json")
      .pipe(map(this.processData, this));
  }

  private processData(data: any) {
    this.screen_privilege = data;
    return this.screen_privilege;
  }
}
